@import './Variables.scss';

.d-flex {
    display: flex;
}
.align-items {
    align-items: center;
}
.justify-content {
    justify-content: center;
}
.d-flex-column {
    display: flex;
    flex-direction: column;
}
.main-wrapper {
    width: 100vw;
    height: 100vh;
    background-color: $primary-color;
    .vertical-line,
    .horizontal-line {
        border-radius: 2px;
        background-color: $tag-color;
    }
    .vertical-line {
        height: 60%;
        width: 1px;
        margin: auto 0px;
    }
    .horizontal-line {
        width: 65%;
        height: 1px;
        margin: 0px auto;
    }
    .main-container {
        height: -webkit-fill-available;
        width: -webkit-fill-available;
        max-height: 708px;
        max-width: 1528px;
        color: #fff;
        padding: 18px;
        background-color: $primary-color;
        .current-weather-info-container {
            width: 40%;
            height: 100%;
            overflow: hidden;
        }
        .forecast-and-air-container {
            width: 60%;
            height: 100%;
            overflow: auto;
        }
    }
}
